import React, { Component } from "react";
import axios from "axios";

import "./NewPost.css";

import HeaderTeacher from "../components/Headers/HeaderTeacher";

class CreateChild extends Component {
  constructor(props) {
    super(props);
    this.state = {
      registration: "",
      parentid: "",
      fullname: "",
      parent: ""
    };
    this.state = { listOfUsers: [] };
  }

  handleFormSubmit = event => {
    event.preventDefault();
    const registration = this.state.registration;
    const parentid = this.state.parentid;
    const fullname = this.state.fullname;
    const parent = this.state.parent;

    axios
      .post(
        `${process.env.REACT_APP_API_URL}/class`,
        { registration, parentid, fullname, parent },
        { withCredentials: true }
      )
      .then(response => {
        this.setState({
          registration: "",
          parentid: "",
          fullname: "",
          parent: ""
        });
        this.props.history.push("/feed");
      })
      .catch(error => console.log(error));
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  handleChangeSelect = event => {
    this.setState({
      parentid: event.target.value
    });
  };

  getAllUsers = () => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/users`, {
        withCredentials: true
      })
      .then(response => {
        this.setState({
          listOfUsers: response.data
        });
      });
  };

  componentDidMount() {
    this.getAllUsers();
  }

  render() {
    return (
      <div>
        <HeaderTeacher></HeaderTeacher>
        <form id="new-post" onSubmit={this.handleFormSubmit}>
          <p> CADASTRE UM ALUNO(A) </p>

          <input
            type="text"
            name="fullname"
            placeholder="Nome da criana"
            onChange={this.handleChange}
            value={this.state.fullname}
            required
          />

          <input
            type="text"
            name="registration"
            placeholder="Insira a matrcula"
            onChange={this.handleChange}
            value={this.state.registration}
            required
          />

          <select onChange={this.handleChangeSelect} name="parentid">
            <option>Selecione o responsvel</option>
            {this.state.listOfUsers.map(user => {
              {
                if (user.role === "parent") {
                  return (
                    <option key={user._id} name="parentid" value={user._id}>
                      {user.fullname}
                    </option>
                  );
                }
              }
            })}
          </select>

          <button type="submit">Cadastrar Aluno(a)</button>
        </form>
      </div>
    );
  }
}

export default CreateChild;
