import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Badge from '@material-ui/core/Badge';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import AccountCircle from '@material-ui/icons/AccountCircle';
import HomeIcon from '@material-ui/icons/Home';
import { Link } from 'react-router-dom';
import PhotoCameraIcon from '@material-ui/icons/PhotoCamera';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import SendIcon from '@material-ui/icons/Send';
import './style.css';

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
}));

export default function AppBarPrimary(props) {


  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
  const handleProfileMenuOpen = event => {
    setAnchorEl(event.currentTarget);
  };
  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };
  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };
  const handleMobileMenuOpen = event => {
    setMobileMoreAnchorEl(event.currentTarget);
  };
  const menuId = 'primary-search-account-menu';

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}

    >

      <Link to='/adduser' style={{ textDecoration: 'none', color: "inherit" }}>
      <MenuItem onClick={handleMenuClose}>Adicionar responsvel</MenuItem></Link>
      <Link to='/addclass' style={{ textDecoration: 'none', color: "inherit" }}>
      <MenuItem onClick={handleMenuClose}>Adicionar aluno(a)</MenuItem></Link>

    </Menu>

  );
  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >

      <Link to='/addclass' style={{ textDecoration: 'none', color: "inherit" }}>
      <MenuItem>
        <IconButton aria-label="show 4 new mails" color="inherit">
          <Badge color="secondary">
          <AccountCircle />
          </Badge>
        </IconButton>
        <p>Adicionar aluno(a)</p>
      </MenuItem>
      </Link>

      <Link to='/adduser' style={{ textDecoration: 'none', color: "inherit" }}>
      <MenuItem>
        <IconButton aria-label="show 11 new notifications" color="inherit">
          <Badge color="secondary">
            <AccountCircle />
          </Badge>
        </IconButton>
        <p>Adicionar responsvel</p>
      </MenuItem>
      </Link>

    </Menu>


  );
  return (
    <div className={classes.grow}>

      <AppBar position="fixed" id="appMenuBar">
        <Toolbar>


          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="open drawer"
          >
          <Link to="/feed" style={{ textDecoration: 'none', color: 'inherit' }}>
          <HomeIcon />
          </Link>
          </IconButton>

          <Typography className={classes.title} variant="h6" noWrap>
           Funny Class
          </Typography>

          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>


        <IconButton aria-label="show 15 new notifications" color="inherit">
              <Badge color="secondary">
                <Link to="/addpost" style={{ textDecoration: 'none', color: 'inherit' }}>
                <PhotoCameraIcon />
              </Link>
              </Badge>
            </IconButton>


            <IconButton color="inherit">
              <Badge color="secondary">
              <Link to="/addpostunique" style={{ textDecoration: 'none', color: 'inherit' }}>
                <SendIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton
              edge="end"
              aria-label="account of current user"
              aria-controls={menuId}
              aria-haspopup="true"
              onClick={handleProfileMenuOpen}
              color="inherit"
            >
              <AccountCircle />
            </IconButton>

            <IconButton aria-label="show 15 new notifications" color="inherit">
              <Badge color="secondary">
                <Link to="/logout" style={{ textDecoration: 'none', color: 'inherit' }}>
                <ExitToAppIcon />
              </Link>
              </Badge>
            </IconButton>
   
          </div>
          <div className={classes.sectionMobile}>


            <IconButton aria-label="show 15 new notifications" color="inherit">
              <Badge color="secondary">
                <Link to="/addpost" style={{ textDecoration: 'none', color: 'inherit' }}>
                <PhotoCameraIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton color="inherit">
              <Badge color="secondary">
              <Link to="/addpostunique" style={{ textDecoration: 'none', color: 'inherit' }}>
                <SendIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton
              aria-label="show more"
              aria-controls={mobileMenuId}
              aria-haspopup="true"
              onClick={handleMobileMenuOpen}
              color="inherit"
            >
              <GroupAddIcon />
            </IconButton>


            <IconButton aria-label="show 15 new notifications" color="inherit">
              <Badge color="secondary">
                <Link to="/logout" style={{ textDecoration: 'none', color: 'inherit' }}>
                <ExitToAppIcon />
              </Link>
              </Badge>
            </IconButton>
          </div>
        </Toolbar>
      </AppBar>

      {renderMobileMenu}
      {renderMenu}


    </div>
  );
}