import React, { Component } from "react";
import axios from "axios";

import "./NewPost.css";

import HeaderTeacher from "../components/Headers/HeaderTeacher";

class CreateUser extends Component {
  constructor(props) {
    super(props);
    this.state = {
      username: "",
      password: "",
      fullname: "",
      email: ""
    };
  }
  handleFormSubmit = event => {
    event.preventDefault();
    const username = this.state.username;
    const password = this.state.password;
    const fullname = this.state.fullname;
    const email = this.state.email;

    axios
      .post(
        `${process.env.REACT_APP_API_URL}/adduser`,
        { username, password, fullname, email },
        { withCredentials: true }
      )
      .then(response => {
        this.setState({
          username: "",
          password: "",
          fullname: "",
          email: ""
        });
        this.props.history.push("/feed");
      })
      .catch(error => console.log(error));
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({
      [name]: value
    });
  };

  render() {
    return (
      <div>
        <HeaderTeacher></HeaderTeacher>
        <form id="new-post" onSubmit={this.handleFormSubmit}>
          <p> CADASTRE UM RESPONSVEL </p>

          <input
            type="text"
            name="fullname"
            placeholder="Nome Completo"
            onChange={this.handleChange}
            value={this.state.fullname}
            required
          />

          <input
            type="text"
            name="email"
            placeholder="Insira o e-mail"
            onChange={this.handleChange}
            value={this.state.email}
            required
          />

          <input
            type="text"
            name="username"
            placeholder="Insira o username"
            onChange={this.handleChange}
            value={this.state.username}
            required
          />

          <input
            type="password"
            name="password"
            placeholder="Insira a senha"
            onChange={this.handleChange}
            value={this.state.password}
            required
          />

          <button type="submit">Cadastrar responsvel</button>
        </form>
      </div>
    );
  }
}

export default CreateUser;
