import React, { Component } from "react";
import api from "../services/api";
import io from "socket.io-client";

import "./Feed.css";

import HeaderParent from "../components/Headers/HeaderParent";

import Lanche from "../assets/icons8-apple-48.png";
import Book from "../assets/icons8-open-book-48.png";
import Note from "../assets/icons8-note-48.png";
import Extra from "../assets/icons8-flag-filled-48.png";
import Alert from "../assets/icons8-error-48.png";
import Problem from "../assets/icons8-health-book-48.png";

import Moment from "react-moment";
import "moment/locale/pt";

class Feed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      feed: []
    };
  }

  async componentDidMount() {
    this.registerToSocket();

    const { params } = this.props.match;

    const response = await api.get(`/postsunique/${params.id}`);

    this.setState({ feed: response.data });
  }

  registerToSocket = () => {
    const socket = io(process.env.REACT_APP_API_URL);

    socket.on("postUnique", newPost => {
      this.setState({ feed: [newPost, ...this.state.feed] });
    });
  };

  render() {
    return (
      <div>
        <HeaderParent user={this.props.loggedInUser}></HeaderParent>
        <section id="post-list">
          {this.state.feed.map(post => (
            <article key={post._id} class="feedunique">
              <header>
                <div className="user-info post-unique">
                  {post.title === "Dia de atividade extra" ? (
                    <img src={Extra} id="alert-logo" alt="" />
                  ) : post.title === "Comendo o lanchinho!" ? (
                    <img src={Lanche} id="alert-logo" alt="" />
                  ) : post.title === "Favor entrar em contato conosco" ? (
                    <img src={Alert} id="alert-logo" alt="" />
                  ) : post.title === "Recadinho" ? (
                    <img src={Note} id="alert-logo" alt="" />
                  ) : post.title ===
                    "Opa! Parece que temos um probleminha..." ? (
                    <img src={Problem} id="alert-logo" alt="" />
                  ) : post.title === "Aprendizado do dia" ? (
                    <img src={Book} id="alert-logo" alt="" />
                  ) : null}

                  <span>{post.title}</span>
                </div>
              </header>

              <footer>
                <div className="actions"></div>
                <p>{post.description}</p>
                <p id="timestamp">
                  <Moment fromNow>{post.createdAt}</Moment>
                </p>
              </footer>
            </article>
          ))}
        </section>
      </div>
    );
  }
}

export default Feed;
