import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import Paper from "@material-ui/core/Paper";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import AuthService from "../components/Auth";

import "./Loginnew.css";

const classes = {
  root: {
    height: "100vh"
  },
  image: {
    backgroundImage:
      "url(https://res.cloudinary.com/projetokids/image/upload/v1572520172/projetokids/loginFinal_nsu9oo.jpg)",
    backgroundSize: "cover",
    backgroundPosition: "center"
  },
  paper: {
    margin: "30px 30px",
    display: "flex",
    flexDirection: "column",
    alignItems: "center"
  },
  avatar: {
    margin: "100px 0px 30px",
    backgroundColor: "#46a9b5"
  },
  form: {
    width: "80%", // Fix IE 11 issue.
    marginTop: "30px"
  },
  submit: {
    margin: "12px 0px 8px"
  }
};

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = { username: "", password: "", message: null };
    this.service = new AuthService();
  }

  handleFormSubmit = event => {
    event.preventDefault();
    const username = this.state.username;
    const password = this.state.password;
    this.service
      .login(username, password)
      .then(response => {
        this.setState({ username: "", password: "" });
        this.props.getUser(response);
        this.props.history.push(
          this.props.location.prevPath ? this.props.location.prevPath : "/feed"
        );
      })
      .catch(error => {
        this.setState({ message: "Usurio e/ou senha invlidos" });
      });
  };

  handleChange = event => {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  render() {
    return (
      <Grid container component="main" style={classes.root}>
        <CssBaseline />
        <Grid item xs={false} sm={4} md={7} style={classes.image} />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <div style={classes.paper}>
            {/* <Avatar style={classes.avatar}>
            <LockOutlinedIcon />
          </Avatar> */}
            <img src="images/icons8-password-48.png" id="logo-login" />
            <Typography component="h1" variant="h5">
              Login
            </Typography>
            <form
              onSubmit={this.handleFormSubmit}
              style={classes.form}
              Validate
            >
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                id="username"
                label="Username"
                name="username"
                autoComplete="username"
                autoFocus
                value={this.state.username}
                onChange={e => this.handleChange(e)}
              />
              <TextField
                variant="outlined"
                margin="normal"
                required
                fullWidth
                name="password"
                label="Senha"
                type="password"
                id="password"
                autoComplete="current-password"
                value={this.state.password}
                onChange={e => this.handleChange(e)}
              />
              <Button
                id="btn-login"
                type="submit"
                fullWidth
                variant="contained"
                color="secondary"
                style={classes.submit}
                value="Login"
              >
                Login
              </Button>
              <div className="msg-error">
                <p>{this.state.message ? this.state.message : null}</p>
              </div>
            </form>
          </div>
        </Grid>
      </Grid>
    );
  }
}

export default Login;
