import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Badge from '@material-ui/core/Badge';
import Menu from '@material-ui/core/Menu';
import HomeIcon from '@material-ui/icons/Home';
import { Link } from 'react-router-dom';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import PermMediaIcon from '@material-ui/icons/PermMedia';
import './style.css';

const useStyles = makeStyles(theme => ({
  grow: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    display: 'none',
    [theme.breakpoints.up('sm')]: {
      display: 'block',
    },
  },
  inputRoot: {
    color: 'inherit',
  },
  inputInput: {
    padding: theme.spacing(1, 1, 1, 7),
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: 200,
    },
  },
  sectionDesktop: {
    display: 'none',
    [theme.breakpoints.up('md')]: {
      display: 'flex',
    },
  },
  sectionMobile: {
    display: 'flex',
    [theme.breakpoints.up('md')]: {
      display: 'none',
    },
  },
}));

export default function AppBarPrimary(props) {


  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = React.useState(null);
  const isMenuOpen = Boolean(anchorEl);
  const isMobileMenuOpen = Boolean(mobileMoreAnchorEl);
  const handleMobileMenuClose = () => {
    setMobileMoreAnchorEl(null);
  };
  const handleMenuClose = () => {
    setAnchorEl(null);
    handleMobileMenuClose();
  };
  const menuId = 'primary-search-account-menu';

  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={menuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMenuOpen}
      onClose={handleMenuClose}

    >

    </Menu>



  );
  const mobileMenuId = 'primary-search-account-menu-mobile';
  const renderMobileMenu = (
    <Menu
      anchorEl={mobileMoreAnchorEl}
      anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
      id={mobileMenuId}
      keepMounted
      transformOrigin={{ vertical: 'top', horizontal: 'right' }}
      open={isMobileMenuOpen}
      onClose={handleMobileMenuClose}
    >
    </Menu>


  );
  return (
    <div className={classes.grow}>


      <AppBar position="fixed" id="appMenuBar">
        <Toolbar>


          <IconButton
            edge="start"
            className={classes.menuButton}
            color="inherit"
            aria-label="open drawer"
          >
          <Link to="/feed" style={{ textDecoration: 'none', color: 'inherit' }}>
          <HomeIcon />
          </Link>
          </IconButton>

          <Typography className={classes.title} variant="h6" noWrap>
           Funny Class
          </Typography>


          <div className={classes.grow} />
          <div className={classes.sectionDesktop}>

            <IconButton color="inherit">
              <Badge color="secondary">
              <Link to="/feed" style={{ textDecoration: 'none', color: 'inherit' }}>
                <PermMediaIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton color="inherit">
              <Badge color="secondary">
              <Link to={`/feed/${props.user._id}`}  style={{ textDecoration: 'none', color: 'inherit' }}>
                <MailOutlineIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton aria-label="show 15 new notifications" color="inherit">
              <Badge color="secondary">
                <Link to="/logout" style={{ textDecoration: 'none', color: 'inherit' }}>
                <ExitToAppIcon />
              </Link>
              </Badge>
            </IconButton>
   

          </div>
          <div className={classes.sectionMobile}>


          <IconButton color="inherit">
              <Badge color="secondary">
              <Link to="/feed" style={{ textDecoration: 'none', color: 'inherit' }}>
                <PermMediaIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton color="inherit">
              <Badge color="secondary">
              <Link to={`/feed/${props.user._id}`} style={{ textDecoration: 'none', color: 'inherit' }}>
                <MailOutlineIcon />
              </Link>
              </Badge>
            </IconButton>

            <IconButton aria-label="show 15 new notifications" color="inherit">
              <Badge color="secondary">
                <Link to="/logout" style={{ textDecoration: 'none', color: 'inherit' }}>
                <ExitToAppIcon />
              </Link>
              </Badge>
            </IconButton>

          </div>
        </Toolbar>
      </AppBar>

      {renderMobileMenu}
      {renderMenu}

    </div>
  );
}