 import React from 'react';

import { Switch, Route } from "react-router-dom";
import AuthService from "./components/Auth";
import ProtectedRoute from "./components/ProtectedRoute";
import Login from "./pages/Login";

import * as moment from "moment";

import FeedParent from "./pages/FeedParent";
import New from "./pages/NewPost";
import Home from "./pages/Home/Home";
import CreateUser from "./pages/CreateUser";
import FeedTeacher from './pages/FeedTeacher';
import CreateChild from './pages/CreateChild';
import AddPostUnique from './pages/AddPostUnique';
import FeedUnique from './pages/FeedUnique';
import Logout from './pages/Logout';

class App extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        loggedInUser: null
      };
      this.service = new AuthService();
    }
  
  
    fetchUser = () => {
      if (this.state.loggedInUser === null) {
        this.service
          .loggedin()
          .then(response => {
            this.setState({
              loggedInUser: response
            });
          })
          .catch(err => {
            this.setState({
              loggedInUser: false
            });
          });
      }
    }
  
    
    componentDidMount() {
      this.fetchUser();
    }
  
  
    getTheUser = (userObj) => {
      this.setState({
        loggedInUser: userObj
      });
    }
  
    render() {
      if (this.state.loggedInUser && this.state.loggedInUser.role === "parent") {
        return (
          <div className="App">
        <Switch>
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path="/feed"                 
                component = { FeedParent }/>
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path="/feed/:id" 
                component = { FeedUnique }/>
            <Route
              exact path="/login"
              render={props => <Login getUser={this.getTheUser} {...props} />}
            />
            <Route exact path="/"
              render={props => <Home getUser={this.getTheUser} {...props} />}/>
            <Route exact path="/logout"
              render={props => <Logout getUser={this.getTheUser} {...props} />}/>
        </Switch>
        </div>
        )} else if (this.state.loggedInUser ) {
          if (this.state.loggedInUser.role === "teacher" || this.state.loggedInUser.role === "scholl") {
            return (
                <div className="App">
        <Switch>
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path="/adduser"                 
                component = { CreateUser }/> 
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path="/addclass"                 
                component = { CreateChild }/> 
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path = "/addpost"
                component = { New } /> 
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path = "/addpostunique"
                component = { AddPostUnique } /> 
            <Route exact path="/"
                render={props => <Home getUser={this.getTheUser} {...props} />}/>
            <ProtectedRoute
                user={this.state.loggedInUser} 
                getUser={this.getTheUser}
                {...this.props}
                exact path="/feed"                 
                component = { FeedTeacher }/>
            <Route
                exact path="/login"
                render={props => <Login getUser={this.getTheUser} {...props} />}
            />
            <Route exact path="/"
                render={props => <Home getUser={this.getTheUser} {...props} />}/>
            <Route exact path="/logout"
                render={props => <Logout getUser={this.getTheUser} {...props} />}/>
        </Switch>
              </div>
        ) 
        }
       } else {
      return (
          <div className="App">
        <Switch>
            <Route
                exact
                path="/login"
                render={props => <Login getUser={this.getTheUser} {...props} />}
              />
            <Route exact path="/"
            render={props => <Home getUser={this.getTheUser} {...props} />}/>
        </Switch>
            </div>
      )
    }
    }
}
    

export default App;