import React, { Component } from "react";

import "./NewPost.css";
import axios from "axios";
import HeaderTeacher from "../components/Headers/HeaderTeacher";

import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import ChildCareIcon from "@material-ui/icons/ChildCare";

class AddPostUnique extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      description: "",
      childparentid: "",
      listOfChildren: [],
      checkbath: true
    };
  }

  handleFormSubmit = event => {
    event.preventDefault();
    const title = this.state.title;
    const description = this.state.description;
    const childparentid = this.state.childparentid;

    axios
      .post(
        `${process.env.REACT_APP_API_URL}/postsunique`,
        { title, description, childparentid },
        { withCredentials: true }
      )
      .then(response => {
        console.error(response);
        this.setState({
          title: "",
          description: "",
          childparentid: ""
        });
        this.props.history.push("/feed");
        // window.location.reload("/feed");
      })
      .catch(error => console.log(error));
  };

  handleChange = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  getAllChildren = () => {
    axios
      .get(`${process.env.REACT_APP_API_URL}/class`, {
        withCredentials: true
      })
      .then(response => {
        this.setState({
          listOfChildren: response.data
        });
      });
  };

  componentDidMount() {
    this.getAllChildren();
  }

  handleChangeSelect = event => {
    this.setState({
      childparentid: event.target.value
    });
  };

  handleSelect = event => {
    this.setState({
      title: event.target.value
    });
  };

  render() {
    return (
      <div>
        <HeaderTeacher></HeaderTeacher>
        <form id="new-post" onSubmit={this.handleFormSubmit}>
          <p>SELECIONE A CRIANA</p>
          <div class="children">
            {this.state.listOfChildren.map(child => {
              return (
                <div key={child._id} id="gridchild">
                  |&ensp;{" "}
                  <FormControlLabel
                    control={
                      <Checkbox
                        icon={<ChildCareIcon />}
                        onChange={this.handleChangeSelect}
                        checkedIcon={<ChildCareIcon />}
                        name="childparentid"
                        value={child.parentid}
                      />
                    }
                    label={child.fullname}
                  />
                  &ensp;
                </div>
              );
            })}
          </div>

          <p> ESCOLHA A ATIVIDADE </p>

          <div class="btn-container1">
            <div>
              <label>Estudos</label>
              <div>
                <img
                  src="images/home/icons8-open-book-48.png"
                  id="alert-logo"
                  alt=""
                />
              </div>
              <div id="btn-onoff">
                <input
                  onChange={this.handleSelect}
                  id="switch-flat"
                  class="switch switch--flat"
                  value="Aprendizado do dia"
                  name="title"
                  type="checkbox"
                />
                <label for="switch-flat" />
              </div>
            </div>

            <div>
              <label>Lanche</label>
              <div>
                <img
                  src="images/home/icons8-apple-48.png"
                  id="alert-logo"
                  alt=""
                />
              </div>
              <div id="btn-onoff2">
                <input
                  onChange={this.handleSelect}
                  id="switch-flat2"
                  class="switch switch--flat2"
                  value="Comendo o lanchinho!"
                  name="title"
                  type="checkbox"
                />
                <label for="switch-flat2" />
              </div>
            </div>

            <div>
              <label id="label2">Extras</label>
              <div>
                <img
                  src="images/home/icons8-flag-filled-48.png"
                  id="alert-logo3"
                  alt=""
                />
              </div>
              <div id="btn-onoff3">
                <input
                  onChange={this.handleSelect}
                  id="switch-flat3"
                  class="switch switch--flat3"
                  value="Dia de atividade extra"
                  name="title"
                  type="checkbox"
                />
                <label for="switch-flat3" />
              </div>
            </div>
          </div>

          <br></br>
          <div class="btn-container1">
            <div>
              <label>Recado</label>
              <div>
                <img
                  src="images/home/icons8-note-48.png"
                  id="alert-logo"
                  alt=""
                />
              </div>
              <div id="btn-onoff">
                <input
                  onChange={this.handleSelect}
                  id="switch-flat4"
                  class="switch switch--flat4"
                  value="Recadinho"
                  name="title"
                  type="checkbox"
                />
                <label for="switch-flat4" />
              </div>
            </div>
            <div>
              <label>Dodi</label>
              <div>
                <img
                  src="images/home/icons8-health-book-48.png"
                  id="alert-logo"
                  alt=""
                />
              </div>
              <div id="btn-onoff2">
                <input
                  onChange={this.handleSelect}
                  id="switch-flat5"
                  class="switch switch--flat5"
                  value="Opa! Parece que temos um probleminha..."
                  name="title"
                  type="checkbox"
                />
                <label for="switch-flat5" />
              </div>
            </div>

            <div>
              <label id="label2">Alerta</label>
              <div>
                <img src="images/home/icons8-error-48.png" alt="" />
              </div>
              <div id="btn-onoff3">
                <input
                  onChange={this.handleSelect}
                  id="switch-flat6"
                  class="switch switch--flat6"
                  value="Favor entrar em contato conosco"
                  name="title"
                  type="checkbox"
                />
                <label for="switch-flat6" />
              </div>
            </div>
          </div>

          <input
            id="comment"
            type="text"
            name="description"
            placeholder="Quer comentar algo?"
            onChange={this.handleChange}
            value={this.state.description}
          />

          <button type="submit">Enviar</button>
        </form>
      </div>
    );
  }
}

export default AddPostUnique;
