import React, { Component } from "react";
import api from "../services/api";
import io from "socket.io-client";

import "./Feed.css";

import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import HighlightOffIcon from "@material-ui/icons/HighlightOff";
import FavoriteIcon from "@material-ui/icons/Favorite";

import HeaderTeacher from "../components/Headers/HeaderTeacher";

import Moment from "react-moment";
import "moment/locale/pt";

class FeedTeacher extends Component {
  state = {
    feed: []
  };

  async componentDidMount() {
    this.registerToSocket();

    this.getAllFeeds();
  }

  getAllFeeds = async () => {
    const response = await api.get("posts");

    this.setState({ feed: response.data });
  };

  registerToSocket = () => {
    const socket = io(process.env.REACT_APP_API_URL);

    //Post e Like
    socket.on(
      "post",
      newPost => {
        this.setState({ feed: [newPost, ...this.state.feed] });
      },
      () => {}
    );

    socket.on("like", likedPost => {
      this.setState(
        {
          feed: this.state.feed.map(post =>
            post._id === likedPost._id ? likedPost : post
          )
        },
        () => {}
      );
    });
  };

  handleLike = id => {
    api.post(`/posts/${id}/like`);
  };

  handleDelete = id => {
    api.delete(`/posts/${id}/delete`).then(() => {
      this.getAllFeeds();
    });
    this.props.history.push("/feed");
    // window.location.reload("/feed");
  };

  render() {
    return (
      <div>
        <HeaderTeacher></HeaderTeacher>
        <section id="post-list">
          {this.state.feed.map(post => (
            <article key={post._id}>
              <header>
                <div className="user-info">
                  <span>
                    <strong>{post.author}</strong>
                  </span>
                </div>
                <FormControlLabel
                  control={
                    <Checkbox
                      icon={<HighlightOffIcon />}
                      onClick={() => this.handleDelete(post._id)}
                      checkedIcon={<HighlightOffIcon />}
                    />
                  }
                />
              </header>

              <img src={post.image} alt="" />

              <footer>
                <div className="actions">
                  <FormControlLabel
                    control={
                      <Checkbox
                        icon={<FavoriteIcon />}
                        onClick={() => this.handleLike(post._id)}
                        checkedIcon={<FavoriteIcon />}
                      />
                    }
                  />
                </div>
                <strong>{post.likes} curtidas</strong>
                <p>{post.description}</p>
                <p>
                  <span>{post.hashtags}</span>
                </p>

                <p id="timestamp">
                  <Moment fromNow>{post.createdAt}</Moment>
                </p>
              </footer>
            </article>
          ))}
        </section>
      </div>
    );
  }
}

export default FeedTeacher;
